@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto',sans-serif;
    background-color: #1a1a1c;
    color: #ffff;
}

.header {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
}

.inputfield{
    padding-left: 16px;
    font-size: 14px;
    width: 260px;
    height: 42px;
    border: none;
    border-radius: 4px;
    box-shadow: 0 0 2pt 3pt navajowhite;
}
.coinContainer{
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    color: #ffff;
}
@media (max-width: 520px){
    .header{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    h1.brand{
        padding-bottom: 16px;
    }
}
.cryptoCoin{
    overflow: hidden;
    text-align: center;
    border-radius: 1rem;
    position: relative;
    width: 320px;
    margin: 2rem;
    box-shadow: 0 3px 3px rgba(0,0,0,0.5);
}

.coinLogo{
    width: 8rem;
    margin-top: 1.2rem;
    height: 40px;
    width: 40px;
    -webkit-clip-path: circle(60px at center);
            clip-path: circle(60px at center);
}

.coinNameWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px;
    font-weight: 700;
}

.coinName{
    font-weight: bold;
    font-size: 1.5rem;
}

.coinSymbol{
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    padding-left: 8px;
}

.priceContainerUp{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #11d811;
}

.priceContainerDown{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #f00600;
}

svg{
    background-color: transparent;
}

.pricechange{
    background-color: transparent;
    padding-left: 10px;
    text-shadow: 0 0 14px black;
}

.coinPrice{
    width: auto;
    background-color: transparent;
}

.coinMarketcap{
    width: auto;
    padding-top: 8px;
    background-color: transparent;
}

.coinVolume{
    background-color: transparent;
    width: auto;
    padding-top: 6px;
    padding-bottom: 8px;
}
